@import url(https://fonts.googleapis.com/css?family=Exo+2|Ubuntu+Mono&display=swap);
html,
body {
  margin: 0;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  min-height: 100vh;
  width: 100%;
}

html {
  font-size: 14px; }

.CodeMirror {
  background-color: #20202b !important;
  width: 100%;
  min-height: 100vh;
  border: 0 !important; }
  .CodeMirror .CodeMirror-line {
    color: #d8d8d8 !important;
    font-family: "Exo 2", sans-serif !important; }
  .CodeMirror .CodeMirror-gutters {
    background-color: #20202b !important;
    color: #d8d8d8 !important;
    border: none !important; }
    .CodeMirror .CodeMirror-gutters .CodeMirror-foldgutter {
      margin: 0 8px; }
  .CodeMirror .CodeMirror-code .cm-comment {
    background: #313142 !important;
    color: #086dd6 !important;
    font-family: "Ubuntu Mono", monospace; }
    .CodeMirror .CodeMirror-code .cm-comment.CodeMirror-selectedtext {
      color: white !important;
      background: #086dd6 !important; }
  .CodeMirror .cm-formatting-list {
    font-family: "Exo 2", sans-serif !important; }
  .CodeMirror .cm-hmd-list-indent {
    border: none !important; }
  .CodeMirror .cm-formatting-task {
    color: white !important;
    background: none !important; }
  .CodeMirror .CodeMirror-line span.hmd-hidden-token.cm-formatting-task {
    color: white !important; }
  .CodeMirror .cm-header {
    font-family: "Exo 2", sans-serif !important;
    color: #d8d8d8 !important; }
    .CodeMirror .cm-header.CodeMirror-selectedtext {
      color: white !important;
      background: #086dd6 !important; }
  .CodeMirror .cm-formatting-header,
  .CodeMirror .cm-formatting-strong,
  .CodeMirror .cm-formatting-em {
    opacity: 0.2; }
  .CodeMirror .cm-variable-2 {
    color: #086dd6 !important; }
    .CodeMirror .cm-variable-2.CodeMirror-selectedtext {
      color: white !important;
      background: transparent; }
  .CodeMirror .cm-link,
  .CodeMirror .cm-string,
  .CodeMirror .cm-keyword {
    color: #086dd6 !important; }
    .CodeMirror .cm-link.CodeMirror-selectedtext,
    .CodeMirror .cm-string.CodeMirror-selectedtext,
    .CodeMirror .cm-keyword.CodeMirror-selectedtext {
      color: white !important;
      background: #086dd6 !important; }
  .CodeMirror .CodeMirror-linenumber {
    color: gray !important; }

.CodeMirror-cursor {
  border-color: #d8d8d8 !important; }

.CodeMirror-selected {
  background: #086dd6 !important; }

.CodeMirror-selectedtext {
  color: white !important;
  background: #086dd6 !important; }

.cm-fat-cursor .CodeMirror-cursor {
  border: 0 !important;
  background: #086dd6 !important; }

.CodeMirror .cm-quote {
  color: #d8d8d8 !important;
  opacity: 0.6; }

